<template>
    <div>
        
        <el-form
                :rules="rules"
                ref="loginForm"
                v-loading="loading"
                element-loading-text="正在登录..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :model="loginForm"
                class="loginContainer">
                <div class="avatar_box">
                   <img src="../assets/ha.png" alt="">
                </div>
            <h3 class="loginTitle">系统登录</h3>
            <el-form-item prop="username">
                <el-input size="normal" type="text" v-model="loginForm.username" auto-complete="off"
                          placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input size="normal" type="password" v-model="loginForm.password" auto-complete="off"
                          placeholder="请输入密码" @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-checkbox size="normal" class="loginRemember" v-model="checked">记住我</el-checkbox>
            <!-- 按钮区域 -->
            <el-form-item class="btns">
                <el-button size="normal" type="primary"  @click="submitLogin">登录</el-button>
                <el-button size="normal" type="info"  @click="resetLoginForm()">重置</el-button>
            </el-form-item>
        </el-form>
    
    </div>
</template>

<script>

    export default {
        name: "Login",
        data() {
            return {
                loading: false,
                loginForm: {
                    username: '',  //admin
                    password: ''   //123
                },
                checked: true,
                rules: {
                    username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{required: true, message: '请输入密码', trigger: 'blur'}]
                }
            }
        },
        methods: {
            //重置功能
            resetLoginForm(){
                // console.log(this)
                this.$refs.loginForm.resetFields();


            },
            submitLogin() {
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        this.postKeyValueRequest('/doLogin', this.loginForm).then(resp => {
                            this.loading = false;
                            if (resp) {
                                this.$store.commit('INIT_CURRENTHR', resp.obj);
                                window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
                                let path = this.$route.query.redirect;
                                this.$router.replace((path == '/' || path == undefined) ? '/home' : path);
                            }
                        })
                    } else {
                        this.$message.error('请输入所有字段');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style>



    .loginContainer {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 15px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
        
       
    }

    .loginTitle {
        margin: 15px auto 20px auto;
        text-align: center;
        color: #505458;
        
    }

    .loginRemember {
        text-align: left;
        margin: 0px 0px 15px 0px;
    }
    
     .avatar_box {
        height: 130px;
        widows: 130px;
        /* border: 1px solid #eee; */
        box-shadow: 0,0 10px #ddd;
        padding: 10px;
        position: absolute;
        left: 50%;
        transform: translate(-50%, -60%);


    }
    img {
        height: 100%;
        widows: 100%;
        border-radius: 50%;
        background-color: #eee;
        box-shadow: 0,0 10px #ddd;
    }
    .btns {
        display: flex;
        justify-content: flex-end;
    }


    

   
</style>